const matchStatetoProps = (state) => {
  return {
    ings: state.burger.ingredients,
    cnt: state.burger.count,
    price: state.burger.totalPrice,
    error: state.burger.error,
    isAuthenticated: state.auth.token !== null,
  };
};
const matchDispatchtoProps = (dispatch) => {
  return {
    onChangeBun: () => dispatch(actions.changeBun()),
    onIngredientAdded: (type, item) =>
      dispatch(actions.addIngredient(type, item)),
    onIngredientRemoved: (type, item) =>
      dispatch(actions.removeIngredient(type, item)),
    onInitIngredientCounts: () => dispatch(actions.initIngredientCounts()),
    onInitPurchase: () => dispatch(actions.purchaseInit()),
    onSetAuthRedirectPath: (path) =>
      dispatch(actions.setAuthRedirectPath(path)),
  };
};

export default connect(
  matchStatetoProps,
  matchDispatchtoProps
)(withErrorHandler(BurgerBuilder, axios));
