import * as actionTypes from "./actionTypes";
import axios from "../../axios-orders";

export const changeBun = () => {
  return {
    type: actionTypes.CHANGE_BUN,
  };
};

export const addIngredient = (type, item) => {
  return {
    type: actionTypes.ADD_INGREDIENT,
    ingredientType: type,
    ingredientItem: item,
  };
};

export const removeIngredient = (type, item) => {
  return {
    type: actionTypes.REMOVE_INGREDIENT,
    ingredientType: type,
    ingredientItem: item,
  };
};

export const setIngredients = (count) => {
  return {
    type: actionTypes.SET_INGREDIENT_COUNTS,
    count: count,
  };
};

export const fetchCountsFailed = () => {
  return {
    type: actionTypes.FETCH_COUNTS_FAILED,
  };
};

export const initIngredientCounts = () => {
  return (dispatch) => {
    axios
      .get("https://react-burger-twp.firebaseio.com/ingredient-counts.json")
      .then((response) => {
        dispatch(setIngredients(response.data));
      })
      .catch((error) => {
        dispatch(fetchCountsFailed());
      });
  };
};
