import React, { useState } from "react";
import { withRouter } from "react-router-dom";
import { Container, Nav, Navbar } from "react-bootstrap";
import Logo from "../../Logo/Logo";
import "./Toolbar.css";

const Toolbar = (props) => {
  const [expanded, setExpanded] = useState(false);
  const { location } = props;

  const updateRoute = (path) => {
    setExpanded(false);
    props.history.push(path);
  };

  return (
    <Navbar id="toolbar" variant="dark" fixed="top" expand="lg" expanded={expanded}>
      <Container>
        <Navbar.Brand onClick={() => updateRoute("/")}>
          <Logo /> Create Burgers
        </Navbar.Brand>
        <Navbar.Toggle aria-controls="basic-navbar-nav" onClick={() => setExpanded(expanded ? false : "expanded")}  />
        <Navbar.Collapse id="basic-navbar-nav">
          <Nav className="ml-auto" activeKey={location.pathname}>
            <Nav.Link onClick={() => updateRoute("/")}>Burger Builder</Nav.Link>
            {props.isAuth ? (
              <Nav.Link onClick={() => updateRoute("/orders")}>
                My Orders
              </Nav.Link>
            ) : null}
            {props.isAuth ? (
              <Nav.Link
                onClick={() => {
                  updateRoute("/logout");
                }}
              >
                Logout
              </Nav.Link>
            ) : (
              <Nav.Link onClick={() => updateRoute("/auth")}>Login</Nav.Link>
            )}
          </Nav>
        </Navbar.Collapse>
      </Container>
    </Navbar>
  );
};

export default withRouter(Toolbar);
