import * as actionTypes from "../actions/actionTypes";

const INGREDIENT_PRICES = {
  extra: 0.1,
  cheese: 0.2,
  meat: 1,
  sauce: 0,
};

const intialState = {
  ingredients: {
    seeds: true,
    extra: [],
    cheese: [],
    meat: [],
    sauce: [],
  },
  totalPrice: 3,
  count: null,
  error: false,
  building: false,
};

const reducer = (state = intialState, action) => {
  let old;
  let updatedCount;
  let updated;
  switch (action.type) {
    case actionTypes.CHANGE_BUN:
      return {
        ...state,
        ingredients: {
          ...state.ingredients,
          seeds: !state.ingredients.seeds,
        },
      };
    case actionTypes.ADD_INGREDIENT:
      old = state.ingredients[action.ingredientType];
      updatedCount = { ...state.count };
      updatedCount[action.ingredientItem] += 1;
      if (action.ingredientItem === "lettuce") {
        let lettuceArray = ["lettuce", "lettuceLight"];
        let randomIndex = Math.floor(Math.random() * lettuceArray.length);

        action.ingredientItem = lettuceArray[randomIndex];
      }
      if (action.ingredientItem === "pepper") {
        let pepperArray = ["pepperYellow", "pepperGreen", "pepperRed"];
        let randomIndex = Math.floor(Math.random() * pepperArray.length);
        action.ingredientItem = pepperArray[randomIndex];
      }
      updated = [action.ingredientItem, ...old];
      if (action.ingredientType === "sauce") {
        const numSauce = state.ingredients.sauce.filter(function (x) {
          return x === action.ingredientItem;
        }).length;

        if (numSauce > 0) {
          updated = [...old];
          updatedCount[action.ingredientItem] = 1;
        }
      }
      return {
        ...state,
        ingredients: {
          ...state.ingredients,
          [action.ingredientType]: updated,
        },
        count: updatedCount,
        totalPrice: (
          state.totalPrice - -INGREDIENT_PRICES[action.ingredientType]
        ).toFixed(2),
        building: true,
      };
    case actionTypes.REMOVE_INGREDIENT:
      old = state.ingredients[action.ingredientType];

      const oldPrice = state.totalPrice;
      updated = old;
      updatedCount = { ...state.count };

      let idx = updated.indexOf(action.ingredientItem);

      if (action.ingredientItem === "pepper") {
        idx = updated.indexOf("pepperYellow");
      }
      let newPrice = oldPrice - -0;
      if (idx > -1) {
        updated.splice(idx, 1);
        newPrice = oldPrice - INGREDIENT_PRICES[action.ingredientType];
        updatedCount[action.ingredientItem] -= 1;
      } else {
        if (action.ingredientItem === "lettuce") {
          idx = updated.indexOf("lettuceLight");
        }
        if (action.ingredientItem === "pepper") {
          idx = updated.indexOf("pepperRed");
        }
        if (idx > -1) {
          updated.splice(idx, 1);
          newPrice = oldPrice - INGREDIENT_PRICES[action.ingredientType];
          updatedCount[action.ingredientItem] -= 1;
        } else {
          if (action.ingredientItem === "pepper") {
            idx = updated.indexOf("pepperGreen");
          }
          if (idx > -1) {
            updated.splice(idx, 1);
            newPrice = oldPrice - INGREDIENT_PRICES[action.ingredientType];
            updatedCount[action.ingredientItem] -= 1;
          }
        }
      }

      return {
        ...state,
        ingredients: {
          ...state.ingredients,
          [action.ingredientType]: updated,
        },
        count: updatedCount,
        totalPrice: newPrice.toFixed(2),
        building: true,
      };
    case actionTypes.SET_INGREDIENT_COUNTS:
      return {
        ...state,
        ingredients: intialState.ingredients,
        totalPrice: intialState.totalPrice,
        count: action.count,
        error: false,
        building: false,
      };
    case actionTypes.FETCH_COUNTS_FAILED:
      return {
        ...state,
        error: true,
      };
    default:
      return state;
  }
};

export default reducer;
